import React, { useState } from "react";
import { useTranslation } from "react-i18next";

function Question() {
  const { t } = useTranslation();
  const [activeAccordion, setActiveAccordion] = useState(0);

  const accordionItems = [
    {
      id: 1,
      question: t("Kurumsal Web Tasarm NEDR?"),
      answer: t("Kurumsal web tasarm, iletmelerin profesyonel kimliini yanstan, modern, kullanc dostu ve mobil uyumlu web sitelerinin hazrlanmasdr. Bu tr tasarmlar, markanzn dijital ortamdaki yz olarak gvenilirlik ve profesyonellik salar.")
    },
    {
      id: 2,
      question: t("Web Tasarm Ajans NEDR ve Neden NEMLDR?"),
      answer: t("Web tasarm ajanslar, kullanc deneyimini n planda tutarak, iletmelerin hedeflerine uygun web siteleri tasarlayan profesyonel firmalardr. Doru bir ajansla almak, markanzn dijital dnyada fark edilmesini salar.")
    },
    {
      id: 3,
      question: t("Web Tasarm FYATLARI Ne Kadar?"),
      answer: t("Web tasarm fiyatlar; sitenin zellikleri, tasarmn karmakl, sayfa says ve ek hizmetlere (SEO, ierik ynetimi, entegrasyonlar) gre deiiklik gsterir. Web Center, her bteye uygun zel zmler sunar.")
    },
    {
      id: 4,
      question: t("En Y Web Tasarm Firmas Nasl SELR?"),
      answer: t("En iyi web tasarm firmasn seerken unlara dikkat etmelisiniz:\nPortfy ve referanslar\nKullanc dostu ve yeniliki tasarmlar\nTeknik destek ve garanti hizmetleri\nProje teslim sresi ve mteri memnuniyeti\nWeb Center olarak bu kriterlerin tamamn karlyoruz.")
    },
    {
      id: 5,
      question: t("Web Tasarm RKET LE almann Avantajlar NELERDR?"),
      answer: t("Profesyonel bir web tasarm irketiyle almak:\n\nletmenizin dijital ortamdaki imajn glendirir.\nArama motorlarnda st sralarda yer almanz salar.\nMobil uyumluluk ve kullanc dostu tasarmlar sunar.")
    },
    {
      id: 6,
      question: t("Neden Web Center Frmas?"),
      answer: t("Web Center olarak sunduumuz ayrcalklar:\n\nTecrbe ve Gven: Yllardr sektrde hizmet veren bir firma olarak mteri memnuniyetine nem veriyoruz.\nzelletirilmi zmler: letmenizin ihtiyalarna zel web tasarmlar hazrlyoruz.\n7/24 Destek: Proje ncesi ve sonrasnda tam destek salyoruz.\nYeniliki Yaklam: Son teknolojileri kullanarak trendleri yakalayan, modern tasarmlar sunuyoruz.\nUygun Fiyatlar: Kaliteli hizmeti eriilebilir fiyatlarla sunuyoruz.")
    }
  ];

  const toggleAccordion = (index) => {
    setActiveAccordion(index);
  };

  return (
    <section className="faqs-section">
      <div className="auto-container">
        <div className="row clearfix">
          <div className="sec-title-twelev text-center">
            <div className="sec-title-twelev__tagline">
              <span className="sec-title-twelev__tagline__bar"></span>
              {t("merak edilenler")}
            </div>
            <h2 className="sec-title-twelev__title">{t("SIKA SORULAN SORULAR")}</h2>
          </div>
          <div className="faq-block col-lg-6 col-md-12 col-sm-12">
            <div className="about-section-three__image">
              <img src="assets/images/faqs/3.webp" alt="FAQ" />
            </div>
          </div>

          <div className="faq-block col-lg-6 col-md-12 col-sm-12">
            <ul className="accordion-box clearfix">
              {accordionItems.map((item, index) => (
                <li 
                  key={item.id} 
                  className={`accordion block ${activeAccordion === index ? 'active-block' : ''}`}
                >
                  <div 
                    className={`acc-btn ${activeAccordion === index ? 'active' : ''}`}
                    style={{ color: '#10adad' }}
                    onClick={() => toggleAccordion(index)}
                  >
                    <span className="count">{item.id}.</span>
                    {item.question}
                  </div>
                  <div className={`acc-content ${activeAccordion === index ? 'current' : ''}`}>
                    <div className="content">
                      <div className="text">{item.answer}</div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Question;
